 * Settings Panel Styles
 */

/* Overlay */
.jp-agent-settings-overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(0, 0, 0, 0.5);
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 10000;
}

/* Dialog */
.jp-agent-settings-dialog {
  background-color: var(--jp-layout-color1);
  border-radius: 8px;
  box-shadow: 0 4px 20px rgba(0, 0, 0, 0.3);
  width: 90%;
  max-width: 500px;
  max-height: 80vh;
  display: flex;
  flex-direction: column;
}

/* Header */
.jp-agent-settings-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 16px 20px;
  border-bottom: 1px solid var(--jp-border-color1);
}

.jp-agent-settings-header h2 {
  margin: 0;
  font-size: 18px;
  font-weight: 600;
  color: var(--jp-ui-font-color1);
}

.jp-agent-settings-close {
  background: none;
  border: none;
  font-size: 24px;
  color: var(--jp-ui-font-color2);
  cursor: pointer;
  padding: 0;
  width: 32px;
  height: 32px;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 4px;
  transition: background-color 0.2s;
}

.jp-agent-settings-close:hover {
  background-color: var(--jp-layout-color2);
  color: var(--jp-ui-font-color1);
}

/* Content */
.jp-agent-settings-content {
  padding: 20px;
  overflow-y: auto;
  flex: 1;
}

.jp-agent-settings-group {
  margin-bottom: 16px;
}

.jp-agent-settings-label {
  display: block;
  margin-bottom: 6px;
  font-size: 13px;
  font-weight: 500;
  color: var(--jp-ui-font-color1);
}

.jp-agent-settings-input,
.jp-agent-settings-select {
  width: 100%;
  padding: 8px 12px;
  font-size: 13px;
  border: 1px solid var(--jp-border-color2);
  border-radius: 4px;
  background-color: var(--jp-layout-color1);
  color: var(--jp-ui-font-color1);
  font-family: var(--jp-ui-font-family);
}

.jp-agent-settings-input:focus,
.jp-agent-settings-select:focus {
  outline: none;
  border-color: var(--jp-brand-color1);
  box-shadow: 0 0 0 1px var(--jp-brand-color1);
}

.jp-agent-settings-input::placeholder {
  color: var(--jp-ui-font-color3);
}

.jp-agent-settings-provider {
  margin-top: 20px;
}

.jp-agent-settings-provider h3 {
  margin: 0 0 16px 0;
  font-size: 15px;
  font-weight: 600;
  color: var(--jp-ui-font-color1);
}

/* Test Result */
.jp-agent-settings-test-result {
  margin: 16px 20px 0;
  padding: 12px;
  border-radius: 4px;
  font-size: 13px;
  background-color: var(--jp-layout-color2);
  border: 1px solid var(--jp-border-color2);
  color: var(--jp-ui-font-color1);
  font-family: var(--jp-code-font-family);
}

/* Footer */
.jp-agent-settings-footer {
  display: flex;
  justify-content: flex-end;
  gap: 8px;
  padding: 16px 20px;
  border-top: 1px solid var(--jp-border-color1);
}

.jp-agent-settings-button {
  padding: 8px 16px;
  font-size: 13px;
  font-weight: 500;
  border-radius: 4px;
  border: none;
  cursor: pointer;
  transition: all 0.2s;
  font-family: var(--jp-ui-font-family);
}

.jp-agent-settings-button-secondary {
  background-color: var(--jp-layout-color2);
  color: var(--jp-ui-font-color1);
}

.jp-agent-settings-button-secondary:hover {
  background-color: var(--jp-layout-color3);
}

.jp-agent-settings-button-test {
  background-color: var(--jp-warn-color1);
  color: white;
}

.jp-agent-settings-button-test:hover:not(:disabled) {
  background-color: var(--jp-warn-color0);
}

.jp-agent-settings-button-test:disabled {
  opacity: 0.5;
  cursor: not-allowed;
}

.jp-agent-settings-button-primary {
  background-color: var(--jp-brand-color1);
  color: white;
}

.jp-agent-settings-button-primary:hover {
  background-color: var(--jp-brand-color0);
}

.jp-agent-settings-button:active {
  transform: translateY(1px);
}
