  font-weight: 600;
  color: #111827;
}

.jp-agent-message-content em {
  font-style: italic;
  color: #6b7280;
}

.jp-agent-message-content ul {
  margin: 8px 0;
  padding-left: 20px;
  list-style-position: inside;
  overflow: hidden;
}

.jp-agent-message-content ol {
  margin: 8px 0;
  padding-left: 20px;
