  padding-left: 8px;
}

.jp-agent-message-content li::marker {
  color: #6366f1;
}

/* Links */
.jp-agent-message-content a {
  color: #2563eb;
  text-decoration: none;
  border-bottom: 1px solid #93c5fd;
  transition: all 0.15s ease;
}

.jp-agent-message-content a:hover {
